<template lang="pug">
    div.labourUnion-detail 
        div.labourUnion-detail-content 
            div.detail-title
                span 
            div.user-content
                div.head-img
                    img(v-lazy="societyImage")
                div.union-alias(v-text="societyName")
                div.union-name(v-text="`${societyChairman}`")
                div.union-person(v-text="`${userCount}`")
        div.text-content
            p(v-text="societyExplain" :class="{'activeDown' : activeName == 'drop-down'}"   )
            div.shrink
               i.icon.iconfont.drop-down(v-if="! isShowFlexible"  @click="dropDown") &#xe60d;
               i.icon.iconfont.up(v-if="isShowFlexible" @click="up") &#xe60c;
        div.crossband
</template>
<script>
export default {
    props: ['societyImage', 'societyName', 'societyChairman', 'userCount', 'societyExplain'],
    data() {
        return {
            isShowFlexible: false,
            activeName: 'drop-down'
        }
    },
    methods: {
        dropDown() {
            this.activeName = '';
            this.isShowFlexible = !this.isShowFlexible;
        },
        up() {
            this.activeName = 'drop-down';
            this.isShowFlexible = !this.isShowFlexible;
        }
    }




}
</script>
<style scoped lang="less">
@import '../../../assets/css/variable.less';
.labourUnion-detail {
    .labourUnion-detail-content {
        height: 13.25rem;
        width: 100%;
        background: url(../../../assets/img/unionBg.jpg) no-repeat;
        background-size: 100% 100%;

        .detail-title {
            font-size: .8rem; // width: 100%;
            height: 1rem;
            line-height: 1rem;
            padding: .65rem .5rem 1.6rem .5rem;
            color: #fff;
            position: relative;
            text-align: center;
        }

        .user-content {
            text-align: center;
            font-size: .7rem;
            color: #fff;
            .head-img {
                height: 4.75rem;
                width: 4.75rem;
                border-radius: 2.5rem;
                margin: 0 auto;
                border: 2px solid #fff;
                img {
                    height: 100%;
                    width: 100%;
                    border-radius: 2.5rem;
                }
            }
            .union-alias {
                padding: .5rem 0;
            }
            .union-person {
                padding: .5rem 0 0 0;
            }
        }
    }
    .text-content {
        padding: .5rem .4rem 0 .4rem;
        background: #fff;
        p {
            color: #333;
            font-size: .6rem;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            overflow: hidden;
        }
        .activeDown {
            display: -webkit-box;
        }
    }
    .shrink {
        text-align: center;
        .iconfont {
            font-size: .6rem;
            color: @fc-light;
        }
    }
}

.crossband {
    height: .5rem;
    width: 100%;
    background-color: #f8f8f8;
}
</style>





