<template lang="pug">
    div.president-detail-content
        //- div.developing ...
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )   
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            //- -
            div.userInformation
                //- -
                div.state(v-if="data.type == 1")
                    span(v-if="data.gh_status == 0" && "data.open_society_code == 0") : 
                    span(v-if="data.gh_status == 1" && "data.open_society_code == 0") : 
                    span(v-if="data.gh_status == 2" && "data.open_society_code == 0") : 
                    span(v-if="data.open_society_code == 1" v-text="`${data.society_code}`")
                //- -
                div.get-out(v-if="data.type == 0" @click="out()")
                    i.icon.iconfont &#xe683;
                //- - 
                div.state(v-if="data.type == 2")
                //- -
                div.join(v-if="data.type == 3" @click="join()")
                    i.icon.iconfont &#xe65b;
                //- -
                div.state(v-if="data.type == 4")
                     span : 
                //- -     
                div.state(v-if="data.type == 5")
                     span : 
                user-information(
                    :societyImage="data.society_image"
                    :societyName="data.society_name"
                    :societyChairman="data.society_chairman"
                    :userCount="data.user_count"
                    :societyExplain="data.society_explain"
                )
            
            //- -
            ul.president-title

                //- -
                template(v-if="data.type == 0")
                    li.member-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 

                //- -
                template(v-if="data.type == 1")
                    li.president-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.president-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 
                    li.president-info-text
                        span(:class="{'active' : listType == 2}" @click="getData(2)") 
                    li.president-info-text
                        span(:class="{'active' : listType == 3}" @click="getData(3)") 

                //- -
                template(v-if="data.type == 2")
                    li.member-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 

                //- -
                template(v-if="data.type == 3")
                    li.member-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 

                //- -
                template(v-if="data.type == 4")
                    li.member-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 

                //- -
                template(v-if="data.type == 5")
                    li.member-info-text
                        span(:class="{'active' : listType == 0}" @click="getData(0)") 
                    li.member-info-text
                        span(:class="{'active' : listType == 1}" @click="getData(1)") 
                        
            //- -status                    
            div.anchorList(v-if="listType == 0")
                anchor-list(:list="list" :isLoaded="isLoaded" :allLoaded="allLoaded" :type="data.type" v-on:chooseActionsData="chooseActionsData")

            div.fansList(v-if="listType == 1")
                fans-list(:list="list" :isLoaded="isLoaded" :allLoaded="allLoaded")

            div.memberList(v-if="listType == 2")
                member-list(:list="list" :isLoaded="isLoaded" :allLoaded="allLoaded" v-on:ToUpdate="ToUpdate")

            div.signOutList(v-if="listType == 3")
                sign-out-list(:list="list" :isLoaded="isLoaded" :allLoaded="allLoaded" v-on:ToUpdate="ToUpdate")

            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
        mt-actionsheet(
            :actions="actionsData"
            v-model="isShowActionsheet"
        )
</template>

<script>
import userInformation from '@/components/society/detail/userInformation'
import anchorList from '@/components/society/detail/anchorList'
import fansList from '@/components/society/detail/fansList'
import memberList from '@/components/society/detail/memberList'
import signOutList from '@/components/society/detail/signOutList'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
import { Toast, MessageBox, } from 'mint-ui'
export default {
    name: 'societyDetail',
    beforeRouteEnter(to, from, next) {   // 
        next(vm => {
            vm.setFooterBarActiveName('societyDetailIndex');
            $(".page-content").scrollTop(0);
        });
    },
    components: {
        userInformation,
        anchorList,
        fansList,
        memberList,
        signOutList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            data: '',
            list: '',
            autoFill: false,
            allLoaded: false,
            topStatus: '',
            bottomStatus: '',
            isLoaded: '',
            page: 2,
            has_next: '',
            society_id: '',
            isShowActionsheet: false,
            actionsData: [],
            currentUserId: '',
            listType: ''
        }
    },
    created() {
        this.getData(0);
    },
    methods: {
        getData(listType) {
            let self = this;
            self.society_id = self.$route.query.society_id;
            self.listType = listType;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_president' : self.api.society_app_president();
            self.axios.get(axiosUrl, {
                params: {
                    society_id: self.society_id,
                    society_status: self.listType
                }
            }).then(res => {
                self.isLoaded = true;
                if (res.data.status == 1) {
                    console.log(res);
                    self.data = res.data;
                    self.list = res.data.list;
                    self.page = 2;
                    self.has_next = res.data.page.has_next;
                    // console.log(typeof self.has_next)
                    self.$nextTick(function() {
                        self.has_next = Number(self.has_next)
                        self.$refs.loadmore.onTopLoaded();
                    });
                }
                else {
                    Toast(res.data.error || '');
                }
            })
                .catch(err => {
                    Toast(err || '');
                    self.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop(id) {
            let self = this;
            //
            self.getData(self.listType);
        },
        loadBottom() {
            console.log(11)
            let self = this;
            if (self.has_next > 0) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_app_president' : self.api.society_app_president();
                self.axios.get(axiosUrl, {
                    params: {
                        page: self.page,
                        society_id: self.society_id,
                        society_status: self.listType
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {
                            self.has_next = res.data.page.has_next;
                            self.page = self.page + 1;
                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            Toast(res.data.error || '');
                        }
                        self.$nextTick(function() {
                            self.$refs.loadmore.onBottomLoaded();
                        });
                    }, 500)
                })
                    .catch(err => {
                        Toast(err || '');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            // 
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 
            this.bottomStatus = status;
        },
        //- 
        out() {
            let message = "" + this.data.society_name;
            MessageBox({
                title: '',
                message: message,
                showCancelButton: true,
            }).then(action => {

                if (action == 'confirm') {
                    let self = this;
                    let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_out' : self.api.society_out();
                    self.axios.get(axiosUrl, {
                        params: {
                            society_id: self.society_id,
                        }
                    }).then(res => {

                        if (res.data.status == 1) {
                            Toast('');
                        }
                        else {

                            Toast(res.data.error || '');
                        }
                    })
                        .catch(err => {
                            Toast(err || '');
                        });
                }

            });

        },
        //-
        join() {
            let message = "" + this.data.society_name;
            MessageBox({
                title: '',
                message: message,
                showCancelButton: true,
            }).then(action => {
                if (action == 'confirm') {
                    let self = this;
                    let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_join' : self.api.society_join();
                    self.axios.get(axiosUrl, {
                        params: {
                            society_id: self.society_id
                        }
                    }).then(res => {
                        console.log(res)
                        if (res.data.status == 1) {
                            Toast('');
                        }
                        else {
                            Toast(res.data.error || '');
                        }
                    })
                        .catch(err => {
                            Toast(err || '');
                        });
                }
            });


        },

        // 
        ToUpdate(dataType) {
            this.getData(dataType)
        },
        outMember() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/society_member_del' : this.api.society_member_del();
            this.axios.get(axiosUrl, {
                params: {
                    member_id: this.currentUserId,
                    society_id: this.society_id
                }
            }).then(res => {
                if (res.data.status == 1) {
                    Toast('');
                }
                else {
                    Toast(res.data.error || '');
                }
            })
                .catch(err => {
                    Toast(err || '');
                })
        },
        showActionsheet() {
            this.isShowActionsheet = !this.isShowActionsheet;
        },
        chooseActionsData(IdentityType, userId) {
            this.showActionsheet();

            this.currentUserId = userId;
            if (IdentityType == 'member') {
                this.actionsData = this.userDetailsData;
            }
            else {
                this.actionsData = this.othersDetailsData;
            }
        }
    },
    watch: {
        list() {
            this.allLoaded = !Boolean(Number(this.has_next));
        }
    },
    computed: {
        othersDetailsData() {
            // 
            let self = this, arrOne = [{
                name: '',
                method: function() {
                    self.$router.push({ path: '/user/others', query: { to_user_id: self.currentUserId } });
                }
            }, {
                name: '',
                method: function() {
                    self.outMember();
                }
            }];
            return arrOne;
        },
        userDetailsData() {
            // 
            let self = this, arrTwo = [{
                name: '',
                method: function() {
                    self.$router.push({ path: '/user/others', query: { to_user_id: self.currentUserId } });
                }
            }];
            return arrTwo;
        }
    }
}
</script>
<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.president-detail-content {
    .userInformation {
        position: relative; //
        .state {
            position: absolute;
            top: .65rem;
            right: .5rem;
            color: #fff;
        } // 
        .get-out {
            position: absolute;
            top: .4rem;
            right: .4rem;
            z-index: 2;
            .iconfont {
                font-size: 1rem;
                color: #fff;
            }
        } //
        .join {
            position: absolute;
            top: .4rem;
            right: .4rem;
            z-index: 2;
            .iconfont {
                font-size: 1rem;
                color: #fff;
            }
        }
    }
    .president-title {
        border-bottom: 1px solid #e6e8f1;
        display: flex; //
        .president-info-text {
            background-color: #fff;
            width: 25%;
            text-align: center;
            font-size: .7rem;
            padding: .775rem 0;
            position: relative;
        } //
        .member-info-text {
            background-color: #fff;
            float: left;
            width: 50%;
            text-align: center;
            font-size: .7rem;
            padding: .775rem 0;
            position: relative;
        }
        li:after {
            content: '';
            display: block;
            height: .8rem;
            width: 1px;
            background-color: #e6e8f1;
            position: absolute;
            left: -2px;
            top: .8rem;
        }
        span {
            padding: 0 .5rem .8rem .5rem;
            color: @fc-light;
            white-space: nowrap;
        }
        .active {
            border-bottom: 2px solid #333;
            color: @color-theme;
            padding: 0 .5rem .8rem .5rem;
            white-space: nowrap;
        }
        .sigu-out {
            span {
                border: 0;
            }
        }
    }
}
</style>




