<template lang="pug">
    div.labourUnion-content 
        template(v-if="isLoaded")
            template(v-if="list && list.length")
                div.labourUnion-list.clearfix
                    //- -
                    div.list-container(v-if="type == 1" v-for="(item,index) in list")
                        div.photo
                            span.sign(v-if="item.user_position == 1") 
                            div.head-img(v-if="item.user_position == 1" @click="userDetails(item.user_id)")
                                img(v-lazy="item.user_image")
                            div.head-img(v-else @click="othersUserDetails(item.user_id)")
                                img(v-lazy="item.user_image")
                        div.union-name
                            span(v-text="item.user_name")
                            div.praise
                                i.icon.iconfont.sex-man(v-if="item.user_sex == 1") &#xe661;
                                i.icon.iconfont.sex-woman(v-if="item.user_sex == 2") &#xe662;
                                img.icon-level(:src="iconLevel[index]")
                    //- -
                    div.list-container(v-if="type != 1" v-for="(item,index) in list")
                        div.photo
                            span.sign(v-if="item.user_position == 1") 
                            div.head-img(@click="userDetails(item.user_id)")
                                img(v-lazy="item.user_image")
                        div.union-name
                            span(v-text="item.user_name")
                            div.praise
                                i.icon.iconfont.sex-man(v-if="item.user_sex == 1") &#xe661;
                                i.icon.iconfont.sex-woman(v-if="item.user_sex == 2") &#xe662;
                                img.icon-level(:src="iconLevel[index]")
                <div v-if="allLoaded" class="no-more-data"></div>        
            template(v-else)
                null-data
</template>
<script>
import { Toast } from 'mint-ui'
import nullData from '@/components/nullData'
export default {
    props: ['list', 'isLoaded', 'allLoaded', 'type'],
    components: {
        nullData
    },
    methods: {
        othersUserDetails(user_id) {
            // 
            this.$emit('chooseActionsData', 'President', user_id);
        },
        userDetails(user_id) {
            // --
            this.$emit('chooseActionsData', 'member', user_id);
        }
    },
    computed: {
        iconLevel() {
            let arr_user_level = [];
            for (let val of this.list) {
                arr_user_level.push(require(`@/assets/img/rank/rank_${val.user_lv}.png`));
            }
            return arr_user_level;
        }
    }

}
</script>

<style lang="less" scoped>
.labourUnion-list {
    width: 100%;
    height: 100%;
    padding: .25rem 0 0 .25rem;
    background-color: #f8f8f8;
    .list-container {
        width: 33.33%;
        float: left;
        padding: 0 5px 5px 0;
        .photo {
            width: 100%;
            height: 100%;
            position: relative;
            img {
                width: 100%;
                height: 100%;
            }
            .sign {
                width: 2rem;
                height: .75rem;
                position: absolute;
                top: .25rem;
                right: .25rem;
                color: #fff;
                font-size: .5rem;
                border: 1px solid #fff;
                text-align: center;
                line-height: .75rem;
                border-radius: 15px;
            }
        }
        .union-name {
            display: flex;
            justify-content: space-between;
            align-items: center;
            span {
                font-size: .65rem;
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            .sex-woman {
                color: #ff71bb;
            }
            .sex-man {
                color: #3fa2ff;
            }
            .praise {
                display: flex;
                align-items: center;
                .iconfont {
                    font-size: .75rem;
                    margin-right: .25rem;
                }
                img {
                    width: 1.3rem;
                    height: .64rem;
                    vertical-align: middle;
                }
            }
        }
    }
}

.clearfix:after {
    display: block;
    content: "";
    height: 0;
    clear: both;
}
</style>



